* {
    box-sizing: border-box;
}

body {
    font-family: "karla", sans-serif;
}

Main {
    padding: 30px;
}

header {
    display: flex;
    align-items: center;
    height: 68px;
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    color: white;
    padding: 20px;
}

.header--image {
    max-width: 70px;
    height: 100%;
    margin-right: 6px;
}

.header--title {
    font-size: 1.25rem;
    margin-right: auto;
}

.header--project {
    font-size: .75rem;
    font-family: "inter", sans-serif;
    font-weight: 500;
}

.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 17px;
    margin-bottom: 17px;
}

.form--inputs {
    font-family: "Karla", sans-serif;
    font-weight: bold;
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    text-indent: 8px;
}

.form--button {
    grid-column: 1/ -1;
    font-family: "karla", sans-serif;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: white;
    cursor: pointer;
}

.form--button:hover {
    filter: brightness(80%);
    transition: ease 250ms;
}

.meme {
    position: relative;
}

.meme--image {
    max-width: 100%;
    border-radius: 3px;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 1.875em;
    font-weight: 400;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    line-height: 1;
    text-shadow: 2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}

/* Tablet Query */ 
@media screen and (min-width: 500px){
    .meme--image {
        display: block;
        margin: 0 auto;
    }

    .meme--text {
        /* font-size: 3.25em; */
    }
}

/* Desktop Query */

@media screen and (min-width: 1025px){
    header {
        padding: 20px 420px;
    }

    .header--project {
        margin-right: 30px;
        font-size: 1rem;
    }

    main {
        padding: 40px 450px;
    }

    .form {
        gap: 25px;
        margin-bottom: 35px;
    }

    .form--inputs {
        padding-top: 15px;
        padding-bottom: 30px;
    } 
    
    .form--button {
        padding-top: 15px;
        padding-bottom: 40px;
        font-size: 1.125rem;
    }

    .meme--text {
        /* font-size: 4em; */
        font-size: 2em;
    }
    
}